/**
* This is a variable font
* You can controll variable axes as shown below:
* font-variation-settings: 'wght' 900.0;
*
* available axes:

* 'wght' (range from 100.0 to 900.0)

*/

@font-face {
  font-family: 'Cabinet Grotesk';
  src: url('./Inter-Web/CabinetGrotesk-Medium.woff2') format('woff2'),
    url('./Inter-Web/CabinetGrotesk-Medium.woff') format('woff'),
    url('./Inter-Web/CabinetGrotesk-Medium.ttf') format('truetype');
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: 'Cabinet Grotesk';
  src: url('./Inter-Web/CabinetGrotesk-Bold.woff2') format('woff2'),
    url('./Inter-Web/CabinetGrotesk-Bold.woff') format('woff'),
    url('./Inter-Web/CabinetGrotesk-Bold.ttf') format('truetype');
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
